.container {
  text-align: center;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search-input {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px 0 0 5px;
  padding: 10px;
  font-size: 16px;
}

.search-button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: 1px solid #ccc;
  border-left: none;
  border-radius: 0 5px 5px 0;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.search-button:hover {
  background-color: #0056b3;
}

.articles-container {
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
  padding: 16px;
  display: grid;
}

.news-article {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 16px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.article-header img {
  max-width: 100%;
  border-radius: 8px;
  margin-bottom: 16px;
}

.article-title a {
  color: #333;
  font-size: 1.25rem;
  font-weight: bold;
  text-decoration: none;
}

.article-title a:hover {
  color: #007bff;
}

.article-author, .article-date {
  color: #777;
  margin: 0;
  font-size: .875rem;
  line-height: 1.6;
}

.article-description {
  color: #555;
  margin-bottom: 16px;
  font-size: 1rem;
}

.article-content {
  color: #333;
  font-size: .875rem;
  line-height: 1.8;
}

.article-content p {
  margin-bottom: 16px;
}

@media (max-width: 1024px) {
  .articles-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .articles-container {
    grid-template-columns: 1fr;
  }
}

.btn_posit {
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn {
  cursor: pointer;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color .3s;
  display: inline-flex;
}

.btn:disabled {
  cursor: not-allowed;
  background-color: #6c757d;
}

.is-hidden {
  display: none;
}

.spinner-border {
  width: 20px;
  height: 20px;
  border: 3px solid rgba(244, 6, 6, .3);
  border-top-color: #fff;
  border-radius: 50%;
  margin-right: 8px;
  animation: spin 1s linear infinite;
}

.btn.loading .spinner {
  visibility: visible;
}

.btn.loading .label {
  visibility: hidden;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

